.profile {
  overflow: hidden;
}

.profile .color {
  padding-bottom: 100px;
  margin-bottom: 130px;
  position: relative;
}

.profile img {
  border-radius: 50%;
  border: 10px solid #fff;
  background-color: #fff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  bottom: -130px;
}

.profile .main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.profile .title {
  max-width: 95%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  text-transform: uppercase;
}

.profile .title h1 {
  max-width: 100%;
  word-wrap: break-word;
  margin: 0;
  font-family: "Source Sans Pro", sans-serif;
}

.controls {
  display: block;
  padding: 7px;
  text-align: right;
}

.controls .fa {
  padding: 8px;
  font-size: 30px;
  color: #fff;  
} .controls .fa:hover {
  cursor: pointer;
  color: #f9f9f9;
}

.options {
  display: block;
  padding: 3ch;
  color: #fff;
  background-color: rgba(0, 0, 0, 0.07);
}

.options h1 {
  margin: 0;
}

.options .item {
  display: block;
}

.options p {
  margin: 0;
  display: inline-block;
  font-size: 20px;
}

.spinning {
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}

@-webkit-keyframes spin {
  0%   {-webkit-transform: rotate(0deg)}
  100% {-webkit-transform: rotate(360deg)}
}

@keyframes spin {
  0%   {-webkit-transform: rotate(0deg)}
  100% {-webkit-transform: rotate(360deg)}
}


